import React, { Component } from 'react';
import LC from 'literallycanvas';
import './literallycanvas.css';
import { Tool } from 'literallycanvas/lib/js/tools/base';


class App extends Component {
  
  render() {
    LC.localize('es_MX')

    const toPrint = () => {
      var dataUrl = document.getElementsByClassName('lc-drawing')[0];
      console.log();
      
      var windowContent = `<!DOCTYPE html>'
                              '<html>'
                              '<head><title>Print canvas</title></head>'
                              '<body>
                                ${dataUrl}
                              '</body>'
                            '</html>`;
      var printWin = window.open('', '', 'width=340,height=260');
      printWin.document.open();
      printWin.document.write(windowContent);
      printWin.document.close();
      printWin.focus();
      printWin.print();
      printWin.close();
    }
    
    LC.defaultTools = [LC.tools.Pencil, LC.tools.Text, LC.tools.Eyedropper, LC.tools.Eraser, LC.tools.Line, LC.tools.Rectangle, LC.tools.Ellipse, LC.tools.Polygon, LC.tools.SelectShape,LC.tools.Pan];
    return (
      <div>
        <button onClick={toPrint}>Imprimir</button>
        <LC.LiterallyCanvasReactComponent
        imageURLPrefix = "/img"
        backgroundColor = "#fff"
        tools = {
          LC.defaultTools
        }
        />
      </div>
    );
  }
}

export default App;
